  showButtons() {
    if (this.state.captions.length === 0 && this.state.urlYouTuBe.length > 0) {
      let urlYouTuBe = this.state.urlYouTuBe.split("/");

      let captions = [];
      let starts = [];
      let durations = [];
      let response = new Promise((resolve) => {
        const xhr = new XMLHttpRequest(),
          method = "GET",
          url =
            "https://video.google.com/timedtext?&lang=en&v=" +
            urlYouTuBe[3].slice(0, 11);

        xhr.open(method, url, true);
        xhr.onreadystatechange = function () {
          // In local files, status is 0 upon success in Mozilla Firefox
          if (xhr.readyState === XMLHttpRequest.DONE) {
            var status = xhr.status;
            if (status === 0 || (status >= 200 && status < 400)) {
              // The request has been completed successfully
              console.log("OK");
            } else {
              // Oh no! There has been an error with the request!
            }
          }
        };

        xhr.onload = function (e) {
          resolve(xhr.responseText);
        };
        xhr.onerror = function () {
          resolve(undefined);
          console.error("** An error occurred during the XMLHttpRequest");
        };
        xhr.send(null);
      });
      response.then((val) => {
        let string = val
          .replace(new RegExp("&#39;", "g"), "'")
          .replace(new RegExp("&amp;#39;", "g"), "'")
          .replace(new RegExp("&amp;amp;", "g"), "and")
          .split("<transcript>");
        let result = string[1].split("</text>");
        result.pop();
        for (let i = 0; i < result.length; i++) {
          let arr = result[i].split(">");
          let textOfStartDuration = arr[0].split(" ");
          let start = textOfStartDuration[1].split("=")[1];
          let duration = textOfStartDuration[2].split("=")[1];

          captions.push(
            arr[1].replace(new RegExp('[&/\\#,+()$~%.":*?<>{}]', "g"), "")
          );
          starts.push(+start.replace(new RegExp('"', "g"), ""));
          durations.push(+duration.replace(new RegExp('"', "g"), ""));
        }
        let length = captions.length;
        let results = [];
        if (length % 2 === 0) {
          for (let i = 0; i < length; i += 2) {
            results.push(captions[i] + " " + captions[i + 1]);
          }
        } else {
          for (let i = 0; i < length - 1; i += 2) {
            results.push(captions[i] + " " + captions[i + 1]);
          }
          results.push(captions[length - 1]);
        }
        let createAnswer = [];
        let inputArr = [];
        let storeIndex = [];
        let show = [];
        let checkAnswer = [];
        let final_text = [];
        let isPlayArr = [];
        for (let i = 0; i < results.length; i++) {
          let arr = [];
          checkAnswer.push(results[i].split(" "));
          final_text.push(results[i].split(" "));
          inputArr.push(true);
          storeIndex.push(0);
          isPlayArr.push(true);
          show.push(0);
          for (let j = 0; j < results[i].split(" ").length; j++) {
            arr.push("___");
          }
          createAnswer.push(arr);
        }
        final_text.forEach((element) => Shuffle(element));

        this.setState({
          captions: captions,
          starts: starts,
          durations: durations,
          results: results,
          final_text: final_text,
          createAnswer: createAnswer,
          inputArr: inputArr,
          storeIndex: storeIndex,
          show: show,
          checkAnswer: checkAnswer,
          isPlayArr: isPlayArr,
        });
      });
    }
  }

